import Image from 'next/image';

import styles from '@/components/pages/home/main/home-main.module.scss';
import { items } from '@/components/pages/home/main/content';
import ContactUs from '@/components/contact-us';

export default async function HomeMain() {
  const response = await fetch('http://localhost:3001/objects');

  console.log(await response.json());

  return (
    <main className={styles.main}>
      <div className={styles.mainWrapper}>
        {items.map((item, index) => 
          <div className={styles.item} key={index}>
            <div className={styles.itemInfo}>
              {item.type && <span className={styles.itemType}>{item.type}</span>}
              {item.title && <span className={styles.itemTitle}>{item.title}</span>}
              {item.address && <span className={styles.itemAddress}>{item.address}</span>}
              <div className={styles.itemSeparator}></div>
              {item.timeline && <span className={styles.itemTimeline}>{item.timeline}</span>}
              {item.description && <span className={styles.itemDescription}>{item.description}</span>}
              {item.size && <span className={styles.itemSize}>{item.size}</span>}
              {item.capacity && <span className={styles.itemCapacity}>{item.capacity}</span>}
              {item.square && <span className={styles.itemSquare}>{item.square}</span>}
            </div>
            <div className={styles.itemPictureWrapper}>
              <Image
                className={styles.itemPicture}
                src={item.picture}
                alt={item.title}
                width={item.width}
                height={item.height}
              />
            </div>
          </div>
        )}
      </div>
      <ContactUs/>
    </main>
  );
}